import React, {useState} from 'react';
import {View, TouchableOpacity, Image} from 'react-native';
import {Icon} from '@ui-kitten/components';

import styles from '../styles';
import {Recipient} from '../interface';
import TextCM from '../../../../../components/Text';
import {String} from '../../../../../../constants';
import {DateUtils} from '../../../../../../utils';
type Props = {
  item: Recipient;
};

const NoteItem: React.FC<Props> = ({item}) => {
  const [isExpanded, setIsExpanded] = useState(true);

  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <View style={styles.processContainer}>
      <TouchableOpacity
        onPress={() => toggleExpand()}
        style={styles.processHeader}>
        <Icon
          name={
            isExpanded
              ? 'arrow-ios-downward-outline'
              : 'arrow-ios-forward-outline'
          }
          fill={'#FC6B03'}
          width={16}
          height={16}
        />
        <TextCM style={styles.processTitle}>
          {item?.notionName || 'User Task'}
        </TextCM>
      </TouchableOpacity>

      {isExpanded && item.reason && (
        <View style={styles.expandedContainer}>
          <View style={styles.userRow}>
            <Image
              style={styles.avatar}
              source={{
                uri: item?.avatar ? item.avatar : String.DEFAULT_IMAGE,
              }}
            />
            <View style={styles.userInfo}>
              {item?.userName && item?.email && (
                <View style={styles.userDetails}>
                  <TextCM style={styles.commentUser}>{item?.userName}</TextCM>
                  <TextCM style={styles.commentEmail}>
                    {item.email ? `(${item?.email?.split('@')[0]})` : ''}
                  </TextCM>
                  <TextCM style={styles.commentTime}>
                    {item?.signDate
                      ? `  ${DateUtils.convertDateToDDMMYYYY_H_M(
                          item?.signDate,
                        )}`
                      : ''}
                  </TextCM>
                </View>
              )}
              <TextCM style={styles.commentText}>{item?.reason}</TextCM>
            </View>
          </View>
        </View>
      )}
    </View>
  );
};

export default NoteItem;
